import { Row, Col, } from "react-bootstrap";
import { Link } from "react-router-dom";
import PageTitle from "../layouts/PageTitle";
import Moment from "react-moment";

import { useEffect } from "react";
// CONSTANTS
import { SERVICE } from "../../../_constants";

import TableGenerator from "../components/tableGenerator.component";

import { useTranslation } from "react-i18next";
import useServiceContextHook from "../../../_hooks/service.context.hook";
import numeral from "numeral";

function Deposits() {
  const { t } = useTranslation();
  const {
    services: { transaction },
    useService,
  } = useServiceContextHook();

  let service = useService({
    [SERVICE?.BULK_RETRIEVE]: transaction.find,
  });
  const { dispatchRequest } = service;

  useEffect(() => {
    dispatchRequest({
      type: SERVICE?.BULK_RETRIEVE,
      payload: {
        where: { type: "DEPOSIT" },
        // fake: true,
        sudo: true,
      },
    });
  }, []);

  return (
    <>
      <PageTitle activeMenu="Deposits" motherMenu="Wallet management" />
      <header className="mb-4">
        <h3>{t("Deposits List")}</h3>
      </header>
      <Row style={{ marginBottom: 20, width: "100%" }}>
        <Col>
          <div className="input-group search-area right d-lg-inline-flex d-none">
            <input
              type="text"
              className="form-control"
              placeholder={t("Filter in record")}
            />
            <div className="input-group-append">
              <span className="input-group-text">
                <Link to={"#"}>
                  <i className="themify-glyph-162"></i>
                </Link>
              </span>
            </div>
          </div>
        </Col>
        <Col sm="auto" style={{ padding: 0 }}></Col>
      </Row>

      <div style={{ marginBottom: 60 }}>
        <DepositsHistoryTable {...{ service }} />
      </div>
    </>
  );
}
function DepositsHistoryTable({ service }) {
  return (
    <>
      <TableGenerator
        {...{ service }}
        mapping={{
          id: "USER ID",
          createdAt: "joined",
        }}
        omit="*"
        extras={[
          "email",
          "username",
          "type",
          "currency",
          "amount",
          "status",
          "date",
          // "action",
        ]}
        transformers={{
          email: ({ row }) => row?.wallet?.user?.email,
          username: ({ row }) => row?.wallet?.user?.profile?.pname,
          type: ({ row }) => (row?.wallet?.user.active ? "Done" : "Pending"),
          currency: ({ row }) => row?.wallet?.currency,
          amount: ({ row }) => numeral(row?.quantity).format("0,0[.]00"),
          status: ({ row }) => row?.status,
          date: ({ row }) => {
            return <Moment format="YYYY/MM/DD" date={row?.createdAt} />;
          },
        }}
      />
    </>
  );
}

export default Deposits;
